
  <template>
  <div class="my-micro-app">
    <div v-if="matchApp" :id="id" class="my-micro-app__content">
      <div class="my-micro-app__loading">
        <slot name="loading">
          <my-spin loading></my-spin>
        </slot>
      </div>
    </div>
    <slot v-else name="404">
      <my-result status="404"
                 title="404"
                 subtitle="抱歉！您访问的页面不存在。">
      </my-result>
    </slot>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     
     import {MyResult as __MyResult__,MySpin as __MySpin__} from '$ui';
     
     
     
     /**
   * 微应用容器
   * @module $ui/components/my-micro-app
   */

  /**
   * 插槽
   * @member slots
   * @property {string} loading 定义加载中内容
   * @property {string} [404] 定义404内容
   */

  export default {
    name: 'MyMicroApp',
    inject: {
      // 主应用根组件实例
      appMaster: {
        default: null
      }
    },
    props: {
      id: {
        type: String,
        default: 'my-micro-app'
      }
    },
    computed: {
      // 当前匹配的子应用
      matchApp() {
        return this.appMaster && this.appMaster.matchApp
      }
    }
  }
     
     __vue__.use(__MyResult__);
__vue__.use(__MySpin__)
    
  </script>


  