
  <template>
  <my-micro-app></my-micro-app>
</template>

<script>
  
     import __vue__ from 'vue';
     
     import {MyMicroApp as __MyMicroApp__} from '$ui';
     
     
     
     export default {
    inject: {
      myPro: {default: null},
      appMaster: {default: null}
    },
    watch: {
      $route: {
        immediate: true,
        handler() {
          this.addTab()
        }
      }
    },
    methods: {
      addTab() {
        let label = '404'
        const matchApp = this.appMaster?.matchApp
        if (matchApp) {
          label = matchApp.props?.title || '子应用'
        }
        this.myPro.addTab({
          label,
          value: this.$route.path,
          path: '/suit',
          closable: true
        })
      }
    }
  }
     
     __vue__.use(__MyMicroApp__)
    
  </script>

  